<template>
  <div id="content">
    <Haeader title="神影电影"></Haeader>
    <div class="login_body">
      <div>
        <input v-model="list.name" class="login_text" type="text" placeholder="账户/手机号/邮箱" />
      </div>
      <div>
        <input v-model="list.pwd" class="login_text" type="password" placeholder="请输入您的密码" />
      </div>
      <div class="login_btn">
        <input @check="dian" type="submin" value="登录" />
      </div>
      <div class="login_link">
        <a href="#">立即注册</a>
        <a href="#">找回密码</a>
      </div>
    </div>
    <TabBar></TabBar>
  </div>
</template>
<script>
import Haeader from "@/components/Haeader.vue";
import TabBar from "@/components/TabBar.vue";
export default {
  name: "logzc",
  components: {
    Haeader,
    TabBar
  },
  data() {
    return {
      list: [
        {
          name: "",
          pwd: ""
        }
      ]
    };
  },
  methods: {
    dian: function() {
      if (!this.list.name || !this.list.pwd) {
        alert("账号密码不行");
        return false;
      } else {
        alert("成功");
      }
    }
  }
};
</script>
<style scoped>
