  width: 99%;
}
.login_body .login_text {
  width: 99%;
  height: 40px;
  border: none;
  border-bottom: 1px #ccc solid;
  margin: 0 5px;
  outline: none;
}
.login_body .login_btn {
  height: 50px;
  margin: 10px;
}
.login_body .login_btn input {
  height: 50px;
  display: block;
  width: 99%;
  background: #e54847;
  border-radius: 3px;
  border: none;
  text-align: center;
  color: white;
}
.login_body .login_link {
  display: flex;
  justify-content: space-between;
}
.login_body .login_link a {
